import Moment from 'moment';
import { extendMoment } from 'moment-range';

const moment = extendMoment(Moment);
import { useEffect, useState } from 'react';

export const MonthView = ({ firstDay, endDay }) => {
  const [weeksRange, setWeeksRange] = useState(
    Array.from(moment.range(firstDay, endDay).by('week')),
  );

  useEffect(() => {
    weeksRange.map(w => {
      var daysRange = Array.from(
        moment.range(w.startOf('week').toDate(), w.endOf('week').toDate()).by('day'),
      );
      daysRange.map(d => console.log(d.format('DD')));
    });
  }, []);

  return (
    <div className="conatiner-fluid">
      <nav aria-label={moment().year()}>
        <ul className="pagination">
          {moment.months().map((month, index) => (
            <li className="page-item" key={month} id={index + 1}>
              <a className="page-link">{month}</a>
            </li>
          ))}
        </ul>
      </nav>
      <hr className="month-line"></hr>
      <table className="table">
        <thead>
          <tr>
            {moment.weekdays().map(weekDay => (
              <th key={weekDay} className="days-of-week">
                {weekDay}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {weeksRange.map(week => {
            var daysRange = Array.from(
              moment.range(week.startOf('week').toDate(), week.endOf('week').toDate()).by('day'),
            );
            return <td>{week.format('DD')}</td>;
          })}
        </tbody>
      </table>
    </div>
  );
};
